.hero {
  background-image: url("../assets/bg.png");
  background-size: cover;
  height: 90vh;
  text-align: center;
  font-family: var(--ff-primary);
}

.hero-info h1 {
  font-size: 5.5rem;
  color: var(--prim-color);
  padding-top: 2%;
  text-shadow: 2px 2px 2px black;
}

.hero-info p {
  color: #fff;
  font-family: var(--ff-secondary);
  font-weight: 200;
  font-size: 1.5rem;
  padding: 0 5rem;
  text-shadow: 1px 2px 2px black;
  margin-bottom: 20px;
}

.hero-img {
  max-width: 60%;
}
